<template>
  <v-container>
    <v-alert
      border="left"
      close-text="Close Alert"
      color="green accent-4"
      dark
      dismissible
      v-if="this.$route.params.message"
    >
      {{ this.$route.params.message }}
    </v-alert>
    <v-row no-gutters>
      <v-col sm="3" class="pa-3" v-for="customer in customers" :key="customer._id">
        <v-card width="400">
          <v-card-text>
            <div class="font-weight-normal">
              <strong>Ad Soyad: </strong> {{customer.nameSurname}}
            </div>
            <div class="font-weight-normal">
              <strong>Kimlik Numarası: </strong> {{customer.identificationNumber}}
            </div>
            <div class="font-weight-normal">
              <strong>Aylık Gelir: </strong> {{customer.monthlyIncome}}
            </div>
            <div class="font-weight-normal">
              <strong>Telefon Numarası: </strong> {{customer.phoneNumber}}
            </div>
            <div class="font-weight-normal">
              <strong>Doğum Tarihi: </strong> {{customer.birthday}}
            </div>
            <div v-if="customer.deposit != null" class="font-weight-normal">
              <strong>Teminat: </strong> {{customer.deposit}}
            </div>
            <div v-else class="font-weight-normal">
              <strong>Teminat: </strong> ---
            </div>
            <div class="font-weight-normal">
              <strong>Kredi Skoru: </strong> {{customer.creditScore}}
            </div>
            <div class="font-weight-normal">
              <strong>Kredi Limiti: </strong> {{customer.creditLimit}}
            </div>
            <div v-if="customer.creditResult === true" class="font-weight-normal">
              <strong>Kredi Sonucu: </strong> <strong class="green--text">Onaylandı</strong>
            </div>
            <div v-else class="font-weight-normal">
              <strong>Kredi Sonucu: </strong> <strong class="red--text">Reddedildi</strong>
            </div>
            <v-btn
              color="primary"
              outlined
              class="mt-2 mx-2 white--text"
              :to="{ name: 'edit-customer', params: {id: customer._id} }"
            >
              Düzenle
              <v-icon
                right
                dark
              >
                mdi-pen
              </v-icon>
            </v-btn>
            <v-btn
              color="error"
              outlined
              class="mt-2 mx-2 white--text"
              @click="removeCustomer(customer._id)"
            >
              Sil
              <v-icon
                right
                dark
              >
                mdi-delete
              </v-icon>
            </v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import API from "../api";

export default {
  name: "Home",
  data() {
    return {
      customers: [],
    };
  },
  async created() {
    this.customers = await API.getAllCustomer();
  },
  methods: {
    async removeCustomer(id){
      const response = await API.deleteCustomer(id);
      if(response.message_code === "SUCCESS"){
        this.$route.params.message = response.message;
        this.customers = await API.getAllCustomer();
      }
    },
  }
};
</script>
